@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600|Open+Sans:400,400i,600);
/*Change this to eliminate all the unused Montserrat thicknesses*/
.bigbutton{
	background-color: hsl(195, 100%, 35%);
	border: none;
	padding: 10px;
	border-radius: 15px;
	min-width: 200px;
	color: white;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
.bigbutton:hover{
	background-color: hsl(195, 100%, 30%);
}
.bigbutton:focus{
	outline: none;
}
.bigbutton:active{
	background-color: hsl(195, 100%, 25%);
	-webkit-transition: none;
	transition: none;
}
body{
	font-family: Open Sans, Montserrat, sans serif;
	--title-font: Montserrat, Open Sans, sans serif;
	--body-font: Open Sans, Montserrat, sans serif;
}
.gray{
	color: hsl(0, 0%, 50%);
}
.navbar{
	height: 80px;
	border-bottom: 1px solid hsl(0, 0%, 75%);
	text-align: right;
	margin-bottom: 80px;
	position: relative;
}
.navcont{
	display: inline-block;
	position: absolute;
	bottom: 16px;
}
.navright{right: 5%}
.navleft{left: 5%}
.navlink{
	color: hsl(195, 20%, 15%);
	font-family: Montserrat;
	font-size: 20px;
	margin-left: 70px;
	margin-right: 10px;
	display: inline-block;
	font-weight: 500;
}
.navlink:hover{
	color: hsl(195, 100%, 40%);
	text-decoration: none;
}

.mainlogonav{ 
	font-family: Montserrat;
	font-weight: 600;
	font-size: 48px;
	line-height: 48px;
	background: hsl(195, 100%, 50%);
	background: -webkit-linear-gradient(hsl(195, 100%, 55%) 0%, hsl(195, 100%, 45%) 60%, hsl(195, 100%, 44.5%) 61%, hsl(195, 100%, 35%));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	display: inline-block;
	vertical-align: bottom;
	margin-bottom: -3px;
}
.mainlogonav:hover, .mainlogofoot:hover{
	background: hsl(195, 100%, 45%);
	background: -webkit-linear-gradient(hsl(195, 100%, 50%) 0%, hsl(195, 100%, 40%) 60%, hsl(195, 100%, 39.5%) 61%, hsl(195, 100%, 35%));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.shrink{
	font-size: 0.7em;
}
.title{
	text-align: center;
	font-family: var(--title-font);
	font-size: 4em;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	margin-bottom: 60px;
}
.clubtitle{
	font-size: 3em;
	text-align: center;
}
.info{
	margin: 0 50px;
	/*background-color: hsl(0, 0%, 95%);*/
	border: 1px solid hsl(0, 0%, 25%);
	border-radius: 10px;
	padding: 20px 0;
}
.middle{
	background-color: hsl(0, 0%, 95%);
	border-width: 1px 0;
	border-color: hsl(0, 0%, 75%);
	border-style: solid;
	padding: 20px;
}
.nomargin{
	margin: 0;
}
.username{
	color: hsl(0, 0%, 60%);
	text-align: center;
}
.sidead{
	border: 1px solid black;
	flex: 0 0 300px;
	height: 600px;
}
.ql-image{
	max-width: 100% !important;
}
.quillcontent{
	padding: 0 20px;
	margin-top: 20px;
}
.title{
	margin-bottom: 15px;
}
.fullsearchwrap{
	margin-bottom: 5px;
}
.fullresult{
	background-color: hsl(0, 0%, 95%);
	margin-bottom: 3px;
	height: calc(600px - 20vw);
	border: 1px solid transparent;
	border-radius: 5px;
	-webkit-transition: border-color 0.2s ease;
	transition: border-color 0.2s ease;
	overflow: hidden;
}
.fullresult:hover{
	border-color: hsl(0, 0%, 80%);
}
.resultbody{
	padding: 10px 15px;
	opacity: 0.9;
}
.resultlink{
	color: inherit;
}
.resultlink:hover{
	text-decoration: inherit;
	color: inherit;
}
.resulthead{
	padding: 10px 15px;
	border-bottom: 1px solid hsl(0, 0%, 80%);
}
.resulttitle{
	text-transform: uppercase;
	letter-spacing: 0.05em;
	font-family: var(--title-font);
	margin-bottom: 0;
}
.searchbar{
	max-width: 80%;
	width: 600px;
	margin: auto;
}
.searchbarwrap{
	padding: 20px;
	background-color: hsl(200, 100%, 90%);
}
#searchbutton{
	width: calc(25% - 3px);
	min-width: 0;
	min-width: initial;
	border-radius: 0;
	border-radius: 0 10px 10px 0;
	vertical-align: top;
	border: 1px solid hsl(0, 0%, 75%);
}
#searchtext{
	width: 75%;
	height: 46px;
	border: none;
	border-radius: 10px 0 0 10px;
	padding: 3px 8px;
	vertical-align: top;
	border: 1px solid hsl(0, 0%, 75%);
	border-right: none;
}
#searchtext:focus{outline: none}
.sidead{
	border: 1px solid black;
	-ms-flex: 0 0 300px;
	margin: 0 2px;
	flex: 0 0 300px;
	height: 600px;
}
.taglist{
	color: hsl(0, 0%, 50%);
	margin-bottom: 0;
}

