.clubtitle{
	font-size: 3em;
	text-align: center;
}
.info{
	margin: 0 50px;
	/*background-color: hsl(0, 0%, 95%);*/
	border: 1px solid hsl(0, 0%, 25%);
	border-radius: 10px;
	padding: 20px 0;
}
.middle{
	background-color: hsl(0, 0%, 95%);
	border-width: 1px 0;
	border-color: hsl(0, 0%, 75%);
	border-style: solid;
	padding: 20px;
}
.nomargin{
	margin: 0;
}
.username{
	color: hsl(0, 0%, 60%);
	text-align: center;
}
.sidead{
	border: 1px solid black;
	-ms-flex: 0 0 300px;
	flex: 0 0 300px;
	height: 600px;
}
.ql-image{
	max-width: 100% !important;
}
.quillcontent{
	padding: 0 20px;
	margin-top: 20px;
}
.title{
	margin-bottom: 15px;
}